import {
  GET_BANK_DETAILS_REQUEST,
  GET_BANK_DETAILS_SUCCESS,
  GET_BANK_DETAILS_FAILURE,
  EDIT_BANK_DETAILS_REQUEST,
  EDIT_BANK_DETAILS_SUCCESS,
  EDIT_BANK_DETAILS_FAILURE,
} from './constants';

const initialState = {
  bankDetails: {},
};

export default function bankDetailsReducer(
  state = initialState,
  {type, payload},
) {
  switch (type) {
    case GET_BANK_DETAILS_REQUEST:
      return {
        ...state,
      };
    case GET_BANK_DETAILS_SUCCESS:
      return {
        ...state,
        bankDetails: payload.data.bank_details,
      };
    case GET_BANK_DETAILS_FAILURE:
      return {
        ...state,
      };
    case EDIT_BANK_DETAILS_REQUEST:
      return {
        ...state,
      };
    case EDIT_BANK_DETAILS_SUCCESS:
      return {
        ...state,
      };
    case EDIT_BANK_DETAILS_FAILURE:
      return {
        ...state,
      };

    default:
      return state;
  }
}
