import {
  GET_OVERVIEW_REQUEST,
  GET_OVERVIEW_SUCCESS,
  GET_OVERVIEW_FAILURE
} from "./constants";

const initialState = {
  overviewData: {}
};

export default function overviewReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case GET_OVERVIEW_REQUEST:
      return {
        ...state
      };
    case GET_OVERVIEW_SUCCESS:
      return {
        ...state,
        overviewData: payload.data.cards
      };

    case GET_OVERVIEW_FAILURE:
      return {
        ...state
      };

    default:
      return state;
  }
}
