import {RSAA} from 'redux-api-middleware';
import {API_ROOT} from 'Core/constants';
import {
  GET_BANK_DETAILS_REQUEST,
  GET_BANK_DETAILS_SUCCESS,
  GET_BANK_DETAILS_FAILURE,
  EDIT_BANK_DETAILS_REQUEST,
  EDIT_BANK_DETAILS_SUCCESS,
  EDIT_BANK_DETAILS_FAILURE,
} from './constants';

export function getBankDetailsAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/bank/details`,
      method: 'GET',
      body: undefined,
      types: [
        GET_BANK_DETAILS_REQUEST,
        GET_BANK_DETAILS_SUCCESS,
        GET_BANK_DETAILS_FAILURE,
      ],
    },
  };
}

export function editBankDetailsAction(updatedBankDetailsObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/bank/details`,
      method: 'POST',
      body: JSON.stringify(updatedBankDetailsObject),
      types: [
        EDIT_BANK_DETAILS_REQUEST,
        EDIT_BANK_DETAILS_SUCCESS,
        EDIT_BANK_DETAILS_FAILURE,
      ],
    },
  };
}
