import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_OVERVIEW_REQUEST,
  GET_OVERVIEW_SUCCESS,
  GET_OVERVIEW_FAILURE
} from "./constants";

export function getOverviewAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/overview`,
      method: "GET",
      body: JSON.stringify(),
      types: [GET_OVERVIEW_REQUEST, GET_OVERVIEW_SUCCESS, GET_OVERVIEW_FAILURE]
    }
  };
}
