import {
  GET_BASIC_ALL_SUCCESS,
  GET_ATTRIBUTE_FAMILIES_REQUEST,
  GET_ATTRIBUTE_FAMILIES_SUCCESS,
  GET_ATTRIBUTE_FAMILIES_FAILURE
} from "./constants";

const initialState = {
  basicData: {},
  attributeFamilies: {}
};

export default function basicReducer(state = initialState, { type, payload }) {
  switch (type) {
    case GET_BASIC_ALL_SUCCESS:
      return {
        ...state,
        basicData: payload.data
      };

    case GET_ATTRIBUTE_FAMILIES_REQUEST:
      return {
        ...state
      };
    case GET_ATTRIBUTE_FAMILIES_SUCCESS:
      return {
        ...state,
        attributeFamilies: payload.data
      };
    case GET_ATTRIBUTE_FAMILIES_FAILURE:
      return {
        ...state
      };
    default:
      return state;
  }
}
