import {RSAA} from 'redux-api-middleware';
import {API_ROOT} from 'Core/constants';
import {
  GET_ORDER_LIST_REQUEST,
  GET_ORDER_LIST_SUCCESS,
  GET_ORDER_LIST_FAILURE,
  GET_ORDER_DETAILS_REQUEST,
  GET_ORDER_DETAILS_SUCCESS,
  GET_ORDER_DETAILS_FAILURE,
  DOWNLOAD_ORDER_INVOICE_REQUEST,
  DOWNLOAD_ORDER_INVOICE_SUCCESS,
  DOWNLOAD_ORDER_INVOICE_FAILURE,
} from './constants';

export function getOrderListAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/order/list`,
      method: 'GET',
      body: undefined,
      types: [
        GET_ORDER_LIST_REQUEST,
        GET_ORDER_LIST_SUCCESS,
        GET_ORDER_LIST_FAILURE,
      ],
    },
  };
}

export function getOrderDetailsAction(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/order/${id}/view`,
      method: 'GET',
      body: undefined,
      types: [
        GET_ORDER_DETAILS_REQUEST,
        GET_ORDER_DETAILS_SUCCESS,
        GET_ORDER_DETAILS_FAILURE,
      ],
    },
  };
}

export function printOrderInvoice(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/order/${id}/invoice/download`,
      method: 'GET',
      body: undefined,
      types: [
        DOWNLOAD_ORDER_INVOICE_REQUEST,
        DOWNLOAD_ORDER_INVOICE_SUCCESS,
        DOWNLOAD_ORDER_INVOICE_FAILURE,
      ],
    },
  };
}
